import React from 'react'
import styles from './styles.module.css'

export const ReactVecka = ({ operation=null, date=null, weekYear=null }) => {
  switch(operation) {
    case "findWeek":
      return findWeek(date)
      break;
    case "findDate":
      return findDate(weekYear)
      break;
    default:
      return thisWeek()
  }

  function thisWeek(){
    let todaydate = new Date();     
    let oneJan =  new Date(todaydate.getFullYear(), 0, 1); 
    let numberOfDays =  Math.floor((todaydate - oneJan) / (24 * 60 * 60 * 1000));  
    let res = Math.ceil(( todaydate.getDay() + 1 + numberOfDays) / 7); 
    return <div className={styles.weekSection}><h3>This Week </h3> {res}</div>
  }

  function findWeek(date){
    Date.prototype.getWeek = function () {
      var onejan = new Date(this.getFullYear(), 0, 1);
      return Math.ceil((((this - onejan) / 86400000) + onejan.getDay() + 1) / 7);
    };
    var myDate = new Date(date);
  return <div className={styles.weekSection}><h3>Week of {date} is</h3> {myDate.getWeek()}</div>
  }

  function findDate(weekYear){
    const weekNumber = weekYear;
    const weekYearArr = weekNumber.split(" ").map(n => parseInt(n))
    const weekOut = getISOWeek(...weekYearArr) 
    return <div className={styles.weekSection}><h3>Week {weekYear}</h3> {weekOut}</div>
  }

  function getISOWeek(w, y) {
    var simple = new Date(y, 0, 1 + (w - 1) * 7);
    var dow = simple.getDay();
    var ISOweekStart = simple;
    if (dow <= 4)
        ISOweekStart.setDate(simple.getDate() - simple.getDay() + 1);
    else
        ISOweekStart.setDate(simple.getDate() + 8 - simple.getDay());
    const temp = {
      d: ISOweekStart.getDate(),
      m: ISOweekStart.getMonth(),
      y: ISOweekStart.getFullYear(),
    }
    const numDaysInMonth = new Date(temp.y, temp.m + 1, 0).getDate()
    
    return Array.from({length: 7}, _ => {
      if (temp.d > numDaysInMonth){
        temp.m +=1;
        temp.d = 1;
      }      
      return <li className={styles.weekDay}>{new Date(temp.y, temp.m, temp.d++).toUTCString()}</li>
    });
  }


}

